// children Page styles
//

#childrenDetailPageEdit {
  background-color: $color-gray-pale;
  background-size: cover;
  position: relative;
  max-height: 698px;
  height: 100vh;
  label {
    font-size: 0.5em;
    display: block;
    height: 1.7em; }
  input,
  textarea {
    font-family: chalkboard;
    padding: 0.2em 1em;
    text-align: center;
    font-style: italic;
    font-size: 0.8em;
    font-size: 1em;
    color: gray; }

  .children-detail-page {
    &__banner {
      border-color: $color-orange;
      background-color: $color-orange-pale;
      &:after {
        background-color: $color-white;
        position: absolute;
        display: block;
        bottom: -9px;
        content: "";
        height: 7px;
        width: 100%; }
      span {
        background-image: url("../../public/images/children-banner-icon.png");
        background-size: cover;
        display: inline-block;
        vertical-align: sub;
        margin-right: 0.4em;
        height: 21px;
        width: 20px; } } }

  .children-profile {
    @include justify-content(center);
    align-items: flex-start;
    margin-bottom: 1em;
    padding: 1em 1em 0;
    display: flex;
    .first-name,
    .last-name {
      display: inline-block;
      width: 48%; }
    input {
      width: 60%; }
    &__children-button {
      display: inline-block;
      margin-bottom: 3em;
      text-align: center;
      width: 49%; }
    &__profile-picture {
      background-image: url("../../public/images/children-button.png");
      box-shadow: 1px 2px 10px rgba(0,0,0,0.3);
      -webkit-clip-path: circle(60% at 50% 50%);
      clip-path: circle(60% at 50% 50%);
      border: 4px solid $color-white;
      background-size: cover;
      display: inline-block;
      border-radius: 50%;
      position: relative;
      min-width: 100px;
      overflow: hidden;
      height: 100px;
      &::after {
        background-color: $color-gray-medium;
        -webkit-font-smoothing: antialiased;
        color: $color-white;
        font-style: italic;
        text-align: center;
        position: absolute;
        padding: 0.1em 1em 0.2em;
        font-weight: bold;
        content: "Change";
        font-size: 0.8em;
        bottom: 0.5em;
        width: 71%; } }
    &__information {
      display: inline-block;
      margin-left: 1.5em; }
    &__children-name {
      margin: 0.3em auto 0 auto;
      color: $color-gray-medium;
      display: inline-block;
      line-height: 1.1em;
      font-size: 1.3em;
      input {
        padding: 0.2em;
        width: 90%; } }
    &__edit {
      background-color: $color-white;
      justify-content: space-between;
      align-items: center;
      text-align: center;
      color: $color-red;
      padding: 0 1em;
      display: flex;
      .editing {
        margin-left: -9.5em;
        flex-grow: 1; }
      .button {
        -webkit-font-smoothing: antialiased;
        background-color: $color-gray-light;
        color: $color-white;
        display: inline-block;
        padding: 0.2em 0.6em;
        &--save {
          background-color: $color-green-light; }
        &--cancel {
          background-color: $color-gray-dark;
          margin-left: 0.3em; } } }
    &__children-title {
      color: $color-gray-medium;
      font-weight: bold;
      font-size: 0.7em; } }

  .children-details {
    -webkit-font-smoothing: antialiased;
    color: $color-gray-medium;
    padding-top: 2em;
    &__details-row {
      align-items: center;
      margin-bottom: 1em;
      display: flex; }
    &__category {
      display: inline-block;
      font-weight: bold;
      width: 66px; }
    &__info {
      display: inline-block;
      font-style: italic; }
    &--checkboxes {
      border: 2px solid $color-gray-light;
      overflow: scroll;
      padding: 0.5em;
      height: 5em; }
    input {
      padding: 0.3em 1em;
      &[type="number"] {
        width: 2em; } }
    textarea {
      padding: 0.5em;
      resize: none;
      border: 0; }
    .checkbox {
      display: inline-block;
      margin-bottom: 0.3em;
      input[type="checkbox"] {
        display: none;
        &:checked + label {
          background-color: $color-orange-pale;
          color: $color-white; }
        &:checked + label:after {
          display: inline-block;
          opacity: 1; } }
      label {
        background-color: $color-gray-medium;
        padding: 0.2em 0.6em 0 0.2em;
        display: inline-block;
        position: relative;
        border-radius: 3px;
        margin: 0 0.1em;
        color: $color-gray-light;
        font-size: 0.9em;
        float: left;
        &:after {
          position: relative;
          content: "X";
          opacity: 0;
          margin-right: 0.4em;
          float: left;
          color: $color-orange;
          left: 0;
          top: 0; } } } } }
